raul-grid-row {
  display: flex;
  width: 100%;
}

raul-grid-row.raul-grid-row--movable, raul-grid-row.raul-grid-row--movable-by-handle {
  display: block;
}

raul-grid-row.raul-grid-row--movable:hover, raul-grid-row.raul-grid-row--movable-by-handle:hover {
  background-color: #E5F4FF;
}

raul-grid-row.raul-grid-row--movable:hover raul-handle, raul-grid-row.raul-grid-row--movable-by-handle:hover raul-handle {
  opacity: 1;
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
}

raul-grid-row.raul-grid-row--movable:hover {
  cursor: move;
}

raul-grid-row.raul-grid-row--movable-by-handle:hover raul-handle {
  cursor: move;
}